<template>
  <div class="menu-section">
    <label for="side-menu-checkbox">
      <div class="material-icons" id="side-menu-icon">menu</div>
    </label>
    <span><b>{{userdata.name}} {{userdata.surname}}</b>&ensp;({{userdata.score}} балів)</span>
  </div>
  <input type="checkbox" id="side-menu-checkbox" ref='checkbox'>
  <div class="underlay" @click='$refs.checkbox.click()'></div>
  <div class="side-menu">
    <div class='side-menu-modules'>
      <div class='menu-entry' v-for='i in userdata.modules.length' :key='i' @click='gotoModule(i-1)'>
        <div class="menu-entry-icon material-icons">bookmark_border</div>
        <div class="menu-entry-label">{{userdata.modules[i-1]}}</div>
      </div>
    </div>
    <div class='menu-entry'>
      <router-link to='/rating' class='menu-entry-icon material-icons'>star_border</router-link>
      <div class="menu-entry-label">Рейтинг</div>
    </div>
    <div class='menu-entry'>
      <router-link to='/settings' class='menu-entry-icon material-icons'>settings</router-link>
      <div class="menu-entry-label">Налаштування</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'side-menu',
  computed: {
    userdata() { return this.$store.state.userdata }
  },
  methods: {
    gotoModule(index) { 
      localStorage.setItem("currentModuleIndex", index) 
      window.location.reload();
    }
  }
}
</script>

<style lang="scss">
.underlay {
  display: none;
  position: fixed;
  left: 0; top: 0;
  width: 100%;
  height: 100%;
  z-index: 5;
}
.menu-section {
  position: absolute;
  top: 0; left: 0;
  z-index: 1;
  width: 100%;
  height: 3.75em;
  display: flex;
  align-items: center;
  box-sizing: border-box;
  background: rgba(0,0,0,.75);
  color: white;
  padding: 0 5%;
  span { flex-grow: 1; text-align: right; }
}
.side-menu {
  position: fixed;
  display: flex;
  z-index: 6;
  overflow: scroll;
  scrollbar-width: none;
  width: 10em;
  height: 100%;
  padding: 1em;
  left: -400px; top: 0;
  transition: all .5s;
  box-sizing: border-box;
  flex-direction: column;
  background: rgba(#323232, .9);
  &::-webkit-scrollbar { display: none }
  .menu-entry {
    text-align: center;
    padding: 2em 0;
    color: #3232;
    border-bottom: 1px solid rgba(white,.12);
    .menu-entry-icon { 
      color: rgba(white,.7); 
      margin-bottom: .25em; 
    }
    .menu-entry-label {
      color: white;
      font-weight: bold;
    }
  }
}
#side-menu-checkbox {
  appearance: none;
  &:checked ~ .underlay { display: block }
  &:checked ~ .side-menu { left: 0 }
}
#side-menu-icon {
  &:hover { opacity: .5; }
}

</style>