<template>
  <side-menu/>
  <div class='title-trapeze-back'></div>
  <div class="title-trapeze-front"></div>
  <div class="module-title">
    <span class="material-icons">book</span>
    <div class="course-name">ЗНО Річний 2022</div>
    <div class="module-name">Математика – {{module}}</div>
    <progress-strip :ratio='averageRatio'/>
  </div>
  <div class="levels-section">
    <router-link :to='(levels[i-1].type == "quiz") ? "/quiz" : "/theory"'
      v-for='i in levels.length' :key='i'
      :class='levelIsOpened(i-1) ? "level-block" : "level-block disabled"'
      @click='setCurrentLevelIndex(levels[i-1].index)'>
      <div class="progress-bar-wrapper" :style ='!levels[i-1].score ? `background: lightgray` : (levels[i-1].type == "theory") 
        ? `background: #5F9EA0` : `animation: col 1s -${ratios[i-1]}s linear forwards paused`'>
        {{Math.round(ratios[i-1]*100)}}%
      </div>
      <span class="level-block-body">
        <div class="level-name">
          <span>{{(levels[i-1].type == "theory") ? 'Конспект: ' : ''}} {{levels[i-1].name}}</span>
        </div>
        <div class="level-info">
          <span class="level-score" :style='levels[i-1].score ? "color: gray" : "color: lightgray"'>
            {{!levels[i-1].score ? 'Не розпочато' : (levels[i-1].type == "quiz") 
            ? `${levels[i-1].score} / ${levels[i-1].maxscore} балів` : 'Переглянуто' }}
          </span>
          <span class="level-stats">
            <span class="material-icons level-stats-icon">people</span>
            <span class="level-score">{{levels[i-1].total ?? 0}}</span>
          </span>
          <span class="level-stats">
            <span class="material-icons level-stats-icon">data_saver_off</span>
            <span class="level-score">{{Math.round(levels[i-1].avg ?? 0)}}%</span>
          </span>
        </div>
      </span>
    </router-link>
  </div>
</template>

<script>
export default {
  computed: {
    modules() { return this.$store.state.userdata.modules },
    module() { return this.modules[localStorage.currentModuleIndex] },
    levels() { return this.$store.state.levels },
    ratios() { return this.levels.map(a => isNaN(a.score/a.maxscore) ? 0 : a.score/a.maxscore) },
    averageRatio() { return this.ratios.reduce((a,b) => a+b) / this.ratios.length },
    userdata() { return this.$store.state.userdata },
  },
  methods: {
    setCurrentLevelIndex(index) { localStorage.setItem("currentLevelIndex", index) },
    levelIsOpened(index) { return index ? !!this.levels[index-1].score : true },
    gotoModule(index) { 
      localStorage.setItem("currentModuleIndex", index) 
      window.location.reload();
    }
  }
}
</script>

<style lang="scss">
.disabled {
  pointer-events: none;
  opacity: .35;
}
.module-title {
  padding: 3em 1em;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  color: white;
  gap: 1.5em 1em;
  .material-icons {
    font-size: 1.75em !important;
  }
  .course-name {
    font-size: 1.1em;
    flex-grow: 1;
    font-weight: bold;
  }
  .module-name {
    font-size: 2em;
    margin-bottom: .25em;
    width: 100%;
    font-weight: bold;
  }
}
.title-trapeze-back {
  position: absolute;
  top: 0; left: 0;
  z-index: -2;
  width: 100%;
  height: 30em;
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 80%);
  background: linear-gradient(90deg, rgba(#753a88,.9), rgba(#cc2b5e,.6))
}
.title-trapeze-front {
  position: absolute;
  top: 0; left: 0;
  z-index: -1;
  width: 100%;
  height: 30em;
  clip-path: polygon(0 0, 100% 0, 100% 80%, 0 100%);
  background: linear-gradient(90deg, rgba(#753a88,.9), rgba(#cc2b5e,.8)), url('../assets/header.png');
  background-size: cover;
}
.levels-section {
  background: white;
  border-radius: 4px;
  box-shadow: 0 2px 15px 2px rgba(0,0,0,.08);
}
.level-block {
  width: 100%;
  height: 6em;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
.level-icon {
  font-size: 3em !important;
  font-weight: 100 !important;
  color: #323232;
  margin: 0 .4em;
}
.level-block-body {
  height: 6em;
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  justify-content: space-evenly;
  padding-right: 5px;
  border-bottom: 1px solid rgba(0,0,0,.06);
}
.level-name {
  font-weight: bold; 
  font-size: 1.1em;
  margin-bottom: -5px;
}
.level-info {
  display: flex;
  .level-score {
    color: gray;
    flex-grow: 1;
    margin-right: clamp(10px,2vw,20px);
  }
  .level-stats-icon {
    font-size: 1.25em !important;
    vertical-align: text-bottom;
    color: gray;
    margin-right: 5px;
  }
}
.progress-bar-wrapper {
  height: 4em;
  width: 4em;
  margin: 0 1.25em;
  border-radius: 4px;
  box-shadow: 0 0 5px 1px rgba(0,0,0,.24);
  background: white;
  font-weight: bold;
  text-align: center;
  color: white;
  line-height: 4em;
}
</style>